@import url(https://fonts.googleapis.com/css2?family=Urbanist:wght@200;300;400;600&display=swap);
.about__container {
  background-image: url(/react-exam/static/media/couple.60797df0.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 90vh;
  text-align: center;
  margin-top: -1.5em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  place-items: center;
  font-size: 1.5rem;
  text-align: center;
}
.paragraph__container {
  margin: 2em;
}

.mail__to {
  width: 1em;
}
.tmdb__link {
  color: var(--clr-accent);
  text-decoration: none;
}
.copyright {
  text-align: right;
}

.btn {
  font-size: 2rem;
  background-color: var(--clr-primary);
  color: var(--clr-accent);
  padding: 0.5em;
  border: solid 1px var(--clr-accent);
  border-radius: 15px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.btn:hover {
  background-color: var(--clr-secondary);
  cursor: pointer;
}

.movie__container {
  border: solid 4px var(--clr-accent);
  background-color: var(--clr-secondary-opacity);
  border-radius: 10px;
  width: auto;
  margin: auto;
  margin-top: 3em;
  margin-bottom: 2em;
  box-shadow: 5px 5px 5px #212121;
}

.home__container {
  text-align: center;
  margin: 0;
  background-image: url(/react-exam/static/media/chairs.81741049.jpg);
  box-shadow: 0 0 15px 15px black inset;
  background-position: center;
  height: 100vh;
}
.search__container {
  background-size: 50%;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  place-items: center;
  align-content: center;
  margin-top: 3em;
  margin-bottom: 3em;
}
.title {
  display: flex;
  justify-content: center;
  color: var(--clr-accent);
  width: 20em;
  height: 25em;
  padding: 0.5em;
  margin: 1em;
}
.title--img {
  max-width: 40em;
  margin-left: -15em;
}
.title h1 {
  margin-left: -3em;
  text-align: left;
}
.card__container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 3em;
  gap: 3em;
}
.input {
  height: 2rem;
  width: 20rem;
  font-size: 1.3rem;
  margin: 2em;
  border-radius: 5px;
  border: none;
  padding: 0.8em;
}
.result__header {
  font-size: 1.8rem;
  font-weight: 700;
}
.movie__poster:hover {
  cursor: pointer;
}

.poster {
  border-radius: 8px;
  box-shadow: 5px 5px 5px #212121;

  margin: auto;
}
.card {
  width: -webkit-min-content;
  width: min-content;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.four_O_four {
  background-image: url(/react-exam/static/media/ofGravity.f70295b5.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  height: 100vh;
  padding: 5em;
  margin-top: -3em;
}

.movieView__container {
  background-image: url(/react-exam/static/media/popcorn.972bff64.jpg);
  background-size: cover;
  background-position: right bottom;
  text-align: center;
  padding: 1em;
}

.movie__container {
  border: solid 4px var(--clr-accent);
  background-color: var(--clr-secondary-opacity);
  border-radius: 10px;
  width: 55vw;
  margin: auto;
  margin-top: 3em;
  margin-bottom: 2em;
}

.movie__backdrop {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  border-radius: 10px;
  box-shadow: 8px 8px 8px #212121;
}

.movie__data {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1fr, 2fr, 2fr;
  grid-gap: 2em;
  gap: 2em;
}

.movie__title {
  text-align: center;
}
.movie__card {
  grid-column: 2;
  grid-row: 4;
  text-align: center;
  justify-self: end;
}
.overview {
  grid-column: 3;
  grid-row: 4;
  align-self: center;
  justify-self: center;
  width: 25vw;
  font-size: 1.3rem;
  text-align: left;
}
.release__date {
  grid-column-start: 2;
  grid-row: 3;
  justify-self: center;
  font-size: 1.5rem;
}

.voting {
  grid-column-start: 3;
  grid-row: 3;
  text-align: center;
  font-size: 1.5rem;
}

@media screen and (max-width: 950px) {
  .movie__data {
    display: flex;
    flex-direction: column;
    grid-gap: 0.2em;
    gap: 0.2em;
    place-items: center;
    margin-top: 0.2em;
  }
  .overview {
    width: 90%;
    font-size: 1.2rem;
    text-align: center;
  }
  .voting {
    order: -1;
  }
  .release__date {
    order: -1;
  }
}

.video-responsive {
  overflow: hidden;
  padding-bottom: 3em;
  position: relative;
}
.video-responsive iframe {
  height: 50vh;
  width: 45vw;
}

.navDesk__container {
  overflow: hidden;
  position: relative;
  background: linear-gradient(
    to left,
    var(--clr-primary),
    var(--clr-primary-dark)
  );

  height: -webkit-fit-content;

  height: -moz-fit-content;

  height: fit-content;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  justify-content: flex-end;
  place-items: center;
  margin-bottom: 0;
}
.navDesk__container button {
  width: 6em;
  margin-right: 0.5em;
  height: 2.5em;
}

.bg__img {
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  height: auto;
  z-index: -1;
}

.btn--home {
  grid-column-start: 9;
}
.btn--about {
  grid-column-start: 10;
}
.logo {
  grid-column-start: 1;
  margin-left: 1.5em;
  width: 10em;
  cursor: pointer;
}
.logo:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

:root {
  --clr-primary: #852024;
  --clr-primary-dark: #4d0e10;
  --clr-secondary: #103c61;
  --clr-secondary-opacity: #103c61b0;
  --clr-secondary-dark-opacity: #0c243894;
  --clr-accent: #cfb584;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Urbanist", sans-serif;
  background-color: black;
  color: whitesmoke;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

